	module.exports = {"data":{"markdownRemark":{"html":"<p>The clients in this house had a wonderful collection of modern art and some contemporary furniture including a large L-shaped sofa but the room was cold and needed an injection of colour and texture.</p>\n<p>We added clean modern horizontal stripe curtains, sumptuous textured cushions, modern lighting and a very large textured rug to bring the room together.</p>\n<p>The bedroom was totally renovated with a bespoke sliding wardrobe and the layers of curtains provided privacy but blocked out the light when needed.</p>\n<p>Sumptuous cushions and bedside lights were set against the metallic feature wall.</p>","frontmatter":{"date":"January 01, 2009","slug":"2009-contemporary-house","title":"Contemporary House in Bath","cover":"bath08.jpg"}},"galleriesJson":{"images":[{"title":"Horizontal stripe curtains","path":"bath02.jpg"},{"title":"Sumptuous cushions and modern lighting","path":"bath08.jpg"},{"title":"The large textured rug holds the room together","path":"bath10.jpg"},{"title":"Re-upholstered chair and coordinated silk lamp shade","path":"bath04.jpg"},{"title":"Bespoke wardrobes","path":"bath01.jpg"},{"title":"Layers of bedroom curtains","path":"bath03.jpg"},{"title":"Sumptuous cushions and feature wall","path":"bath06.jpg"},{"title":"Stylish bedsite lamps finish the scheme","path":"bath07.jpg"},{"title":"Sheer linens in the hall","path":"bath05.jpg"}]}},"pathContext":{"slug":"2009-contemporary-house"}}

